.card {
    display: flex;
    flex-direction: column;
    width: 100%;
    background-color: #fff;
    box-shadow: 0 2px 4px #F7F7F7;
    border-radius: 5px;
    overflow: hidden;
}
.dark-theme .card {
    
    background-color: var(--dark-Blue-dark);
    box-shadow: 0 2px 4px var(--dark-border-shadow);
    border-radius: 5px;
    overflow: hidden;
}
.card-img {
    width: 256px;
    height: 156px;
    background-color: #f1f1f1;
}
.card-img img {
    width: 100%;
    height: 100%;
}

.cart-content {
    padding: 22px;
    flex: 1;
    font-size: 0.875rem;
    color:#626262;
}
.dark-theme .cart-content {
   
    color: var(--dark-light-gray-text)
}
.dark-theme .cart-content b {
   
    color: var(--dark-white-text);
}
.cart-content h2 {
    color: #000;
    font-size: 1rem;
    font-weight: 700;
    padding-bottom: 9px;

}
.dark-theme .cart-content h2 {
    color: var(--dark-white-text);
   

}
