
.header-wrapper {
    background-color: #fff;
    border-bottom: 2px solid #F7F7F7;
    box-shadow: 0 1px 2px #EFEFEF;
    
}
.dark-theme .header-wrapper {
    background-color: var(--dark-Blue-dark);
    border-bottom: 2px solid var(--dark-border-shadow);
    box-shadow: 0 1px 2px var(--dark-border-shadow);
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    max-width: var(--maxWidth);
    height: 78px;
    margin: 0 auto;
}
@media (max-width:1260px) {
    .header {
        padding: 0px 20px;
    }
}
@media (max-width:550px) {

    .header h1 {
        font-size: 1em;
        margin: 0;
    }

}