

:root {
    --dark-Blue-dark: #2B3743;
    /*(Dark Mode Elements)*/
    --dark-very-Dark-Blue-bgd: #202D36;
    /*(Dark Mode Background)*/
    --dark-white-text: hsl(0, 0%, 100%);
    --dark-light-gray-text: #B9C6CF;
    --dark-border-shadow:#24313C;
    /*(Dark Mode Text & Light Mode Elements)*/
    --light-dark-Gray-input: hsl(0, 0%, 52%);
    /*(Light Mode Input)*/
    --light-very-Light-Gray-bgd: hsl(0, 0%, 98%);
    /*(Dark Mode Text & Light Mode Elements)*/
    --light-very-Dark-Blue-text: hsl(200, 15%, 8%);
    /*(Light Mode Text)*/
}

:root {
    --maxWidth: 1240px;
}

html,
body {
    font-size: 16px;
    min-width: 375px;
    /* background-color: #FAFAFA; */
}

.light-theme {
    color: var(--light-very-Dark-Blue-text);
    background-color: var(--light-very-Light-Gray-bgd);
}

.dark-theme {
    color: var(--dark-white-text);
    background-color: var(--dark-very-Dark-Blue-bgd);
}