.navigate {
    display: inline-flex;
    align-items: center;
    gap: 10px;
    color: #000;
    margin-bottom: 80px;
    padding: 10px 35px;
    border-radius: 5px;
    background-color: #fff;
    box-shadow: 1px 2px 8px rgba(0, 0, 0, 0.4);
}

.dark-theme .navigate {
    color: var(--dark-white-text);
    background-color: var(--dark-Blue-dark);
    box-shadow: 1px 2px 8px var(--dark-border-shadow);
}

.navigate span {
    color: #606261;
    font-weight: 500;
}

.dark-theme .navigate span {
    color: var(--dark-white-text);
}

.detail-main-title h1 {
    font-weight: 700;

}

.detail-main-content,
.detail-main-desc {
    display: flex;
}

.detail-main-left,
.detail-main-right,
.desc-left,
.desc-right {
    flex: 1;
}

.detail-main-left {
    display: flex;
    justify-content: center;
    align-items: center;

}

.flag {
    width: 543px;
    height: 390px;
    min-width: 324px;
    min-height: 230px;

}

.flag img {
    height: 100%;
    width: 100%;
}

.detail-main-desc {
    margin-top: 25px;
    color: #2F2F2F;
}

.dark-theme .detail-main-desc {

    color: var(--dark-light-gray-text);
}

.dark-theme .detail-main-desc b {

    color: var(--dark-white-text);
}

.detail-main-buttons {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-top: 56px;
}

.button-group-content {
    flex: 1;
    display: flex;
    align-items: center;
    gap: 10px;
    flex-wrap: wrap;
}

.detail-main-buttons a {
    display: inline-block;
    padding: 5px 25px;
    font-size: 0.875rem;
    color: #2F2F2F;
    border: 2px solid #EFEFEF;
}

.dark-theme .detail-main-buttons a {

    color: var(--dark-light-gray-text);
    background-color: var(--dark-Blue-dark);
    border-color: var(--dark-Blue-dark);
}

@media (max-width:1260px) {
    .detail-main-content {
        flex-direction: column;
        gap: 20px;
        padding: 50px;
    }
}

@media (max-width:500px) {
    .main-wrapper {
        padding-left:30px;
        padding-right: 30px;
    }
    .detail-main-desc {
        flex-direction: column;
        gap: 20px;
        padding: 0px;
        margin-top: 0px;
    }

    .detail-main-content {
        padding: 0;
    }
    .navigate {
        margin-bottom: 60px;
    }
        .flag {
            width: 543px;
            height: 230px;
            min-width: 324px;
            min-height: 230px;
    
        }
           .detail-main-buttons {
               flex-direction: column;
               align-items: flex-start;
               gap: 10px;
               margin-top: 46px;
           }
}