.filter-box-wrapper {
    display: flex;
    width: 195px;
    height: 55px;
}
:where(.css-dev-only-do-not-override-ph9edi).ant-select-single:not(.ant-select-customize-input) .ant-select-selector {
    display: flex;
    align-items: center;
    height: 100%;
    font-size: 1rem;
}
.dark-theme :where(.css-dev-only-do-not-override-ph9edi).ant-select-single:not(.ant-select-customize-input) .ant-select-selector {
    background-color: var(--dark-Blue-dark);
    color: var(--dark-light-gray-text);
    border-color:var(--dark-border-shadow) ;
}