.theme {
    display: flex;
    align-items: center;
    gap: 12px;
    font-size: 1.1rem;
    padding: 10px;
    background-color: transparent;
}
@media (max-width:400px) {
    .theme {
        display: flex;
        align-items: center;
        gap: 10px;
        font-size: 1em;
        padding: 10px;
        background-color: transparent;
    }
}