.main-wrapper {
    display: flex;
    flex-direction: column;
    max-width: var(--maxWidth);
    margin: 0 auto;
    padding: 44px 0px;
    
}
@media (max-width:1260px) {
    .main-wrapper {
        max-width: 1240px;
        grid-template-columns: repeat(4, 256px);
        gap: 20px;
    }
}